// Dependencies
import React, { useState, useEffect } from 'react';
import { useLocation } from 'react-router-dom';
import { useSelector } from 'react-redux';

// Components
import ModalWelcome from './ModalWelcome';
import ModalMovingGuide from './ModalMovingGuide';
import ModalMovingInfo from './ModalMovingInfo';
import ModalMoveList from '../../components/ModalMoveList';
import ModalSaveMoveList from '../../components/ModalSaveMoveList';

// Common
import { Helper } from '../../../general/common';

// Repositories
import MoveListSaveRepository from 'Repositories/b8one.moveListSaveRepository';

// Services
import CartService from 'Services/b8one.cartService';

const moveListSaveRepository = new MoveListSaveRepository();
const cartService = new CartService();

// SelectItems component
const SelectItems = props => {
  const location = useLocation();
  const [listData, setListData] = useState({});
  const [showModalWelcome, setShowModalWelcome] = useState(false);
  const [showModalMoveList, setShowModalMoveList] = useState(false);
  const [showModalMovingGuide, setShowModalMovingGuide] = useState(false);
  const [showModalMovingInfo, setShowModalMovingInfo] = useState(false);
  const [showModalSaveMoveList, setShowModalSaveMoveList] = useState(false);

  const userEmail = useSelector(({ user }) => user.email);

  useEffect(() => {
    const inputButtonSave = document.querySelector('.js-button-save');

    inputButtonSave.addEventListener('click', e => {
      setShowModalSaveMoveList(true);
    });

    async function fetchAndSetListData() {
      const orderForm = await cartService.getCurrent();
      const orderInfoApp = orderForm.customData?.customApps.find(
        customApp => customApp.id === 'orderinfo'
      );
      if (orderInfoApp) {
        const { moveListSaveSelected: DocumentId } = orderInfoApp.fields;
        if (DocumentId && DocumentId !== 'custom list') {
          const list = await moveListSaveRepository.getListByDocumentId(
            DocumentId
          );
          if (list.isTemplate) {
            setListData(list);
            setShowModalMovingGuide(true);
          } else {
            // const modalWelcomeCookie = Helper.getCookie('modal-welcome-show');
            // if (!modalWelcomeCookie) {
            //   setShowModalWelcome(true);
            // }
          }
        } else {
          if (DocumentId === 'custom list') {
            await window.SelectItems.removeProducts();
          }
          // const modalWelcomeCookie = Helper.getCookie('modal-welcome-show');
          // if (!modalWelcomeCookie) {
          //   setShowModalWelcome(true);
          // }
        }
      }
      if (orderForm.items.length > 0) {
        setShowModalMovingGuide(false);
      } else {
        setShowModalMovingGuide(true);
      }
    }

    fetchAndSetListData();
  }, []);

  useEffect(() => {
    const buttonShowMyMoveLists = document.querySelector(
      '.js-button-show-my-move-lists'
    );

    function handleButtonShowMyMoveListsClick() {
      if (!userEmail) {
        const returnUrl = window.location.search
          ? window.location.search.replace('showMyLists=true', '') +
            '&showMyLists=true'
          : `${window.location.href}?showMyLists=true`;

        window.vtexid.start({
          returnUrl,
          locale: 'pt-BR',
          forceReload: false,
        });

        return;
      }

      setShowModalMoveList(true);
    }

    buttonShowMyMoveLists?.addEventListener(
      'click',
      handleButtonShowMyMoveListsClick
    );

    if (userEmail && location.search.includes('showMyLists=true')) {
      setShowModalMoveList(true);
    }

    return () => {
      buttonShowMyMoveLists?.removeEventListener(
        'click',
        handleButtonShowMyMoveListsClick
      );
    };
  }, [userEmail]);

  const checkCookieForWelcomeModal = () => {
    const modalWelcomeCookie = Helper.getCookie('modal-welcome-show');

    if (!modalWelcomeCookie) {
      setShowModalWelcome(true);
    }
  };

  const handleFirstStep = () => {
    setShowModalMovingGuide(false);
    setShowModalMovingInfo(true);
  };

  const handleReturnStep = () => {
    setShowModalMovingInfo(false);
    setShowModalMovingGuide(true);
  };

  const handleModalMovingGuideClose = () => {
    setShowModalMovingGuide(false);
    setShowModalWelcome(true);
  };

  const handleModalMovingInfoClose = () => {
    setShowModalMovingInfo(false);
    setShowModalWelcome(true);
  };

  return (
    <>
      <ModalWelcome
        showModal={showModalWelcome}
        handleClose={() => {
          setShowModalWelcome(false);

          Helper.setCookie('modal-welcome-show', false, 30);
        }}
      />

      <ModalMovingGuide
        list={listData}
        showModal={showModalMovingGuide}
        handleClose={handleModalMovingGuideClose}
        handleNextStep={handleFirstStep}
      />

      <ModalMovingInfo
        list={listData}
        showModal={showModalMovingInfo}
        handleClose={handleModalMovingInfoClose}
        handleReturn={handleReturnStep}
      />

      <ModalSaveMoveList
        showModal={showModalSaveMoveList}
        setShowModalMoveList={setShowModalMoveList}
        handleClose={() => {
          setShowModalSaveMoveList(false);
        }}
      />

      {showModalMoveList && (
        <ModalMoveList
          showModal={showModalMoveList}
          handleClose={() => {
            setShowModalMoveList(false);
          }}
        />
      )}
    </>
  );
};

export default SelectItems;
