// Dependencies
import React, { useState, useEffect } from 'react';
import classnames from 'classnames';
import { useFormik } from 'formik';
import * as Yup from 'yup';
import dayjs from 'dayjs';
import { useSelector } from 'react-redux';

// Components
import Modal from '../Modal';

// Repositories
import MoveListSaveRepository from 'Repositories/b8one.moveListSaveRepository';

// Yup Schema
const MoveListFormSchema = Yup.object().shape({
  moveListName: Yup.string()
    .min(3, 'Minimo 3 letras')
    .required('Requerido'),
  moveListEmail: Yup.string()
    .email('E-mail invalido')
    .required('Requerido'),
});

const moveListSaveRepository = new MoveListSaveRepository();

// ModalSaveMoveList component
const ModalSaveMoveList = ({
  showModal,
  handleClose,
  setShowModalMoveList,
}) => {
  const [showSuccess, setShowSuccess] = useState(false);
  const [isSavingCustomData, setIsSavingCustomData] = useState(false);

  const userEmail = useSelector(({ user }) => user.email);
  const products = useSelector(({ products }) => products);

  const formik = useFormik({
    validationSchema: MoveListFormSchema,
    onSubmit: async ({ moveListName, moveListEmail }, { setSubmitting }) => {
      const currentFormattedDate = dayjs().format('DD/MM/YYYY');

      const productsWithQuantity = Object.values(products).filter(
        product => product.quantity > 0
      );

      const listItems = JSON.stringify(productsWithQuantity);

      await moveListSaveRepository.addList(
        moveListName,
        moveListEmail,
        currentFormattedDate,
        listItems
      );

      setSubmitting(false);
      setShowSuccess(true);
    },
    initialValues: {
      moveListName: '',
      moveListEmail: '',
    },
  });

  useEffect(() => {
    formik.validateForm();

    document.addEventListener('initSaveCustomData', () =>
      setIsSavingCustomData(true)
    );

    document.addEventListener('endSaveCustomData', () => {
      setIsSavingCustomData(false);
    });
  }, []);

  useEffect(() => {
    setTimeout(() => {
      if (!showModal && showSuccess) {
        setShowSuccess(false);
      }
    }, 400);
  }, [showModal]);

  return (
    <Modal
      handleClose={() => {
        handleClose();
        formik.resetForm();
      }}
      modalClass={classnames('modal-save-move-list', {
        'wrapper-modal--show': showModal,
      })}
    >
      {!showSuccess && (
        <>
          <h2 className={'modal__title'}>Salvar minha lista</h2>

          <p className={'modal__paragraph'}>
            Salve sua lista de itens da mudança e continue editando depois
          </p>

          <form
            onSubmit={formik.handleSubmit}
            className={'modal__form modal__form--without-margin'}
          >
            <div className={'form__container'}>
              <div className={'form__group'}>
                <label className={'form__label'} htmlFor={'moveListName'}>
                  Dê um nome à sua lista
                </label>

                <input
                  type={'text'}
                  autoFocus={true}
                  id={'moveListName'}
                  name={'moveListName'}
                  className={'form__input'}
                  onChange={formik.handleChange}
                  value={formik.values.moveListName}
                  placeholder={'Ex.: Lista de mudança'}
                />
              </div>
            </div>

            <div className={'form__container'}>
              <div className={'form__group'}>
                <label className={'form__label'} htmlFor={'moveListEmail'}>
                  Seu e-mail
                </label>

                <input
                  type={'email'}
                  id={'moveListEmail'}
                  name={'moveListEmail'}
                  className={'form__input'}
                  onChange={formik.handleChange}
                  value={formik.values.moveListEmail}
                  placeholder={'Ex.: enrique@gmail.com'}
                />
              </div>
            </div>

            <div className={'form__container form__container--flex-column'}>
              <button
                type={'submit'}
                className={'button button--primary'}
                disabled={formik.isSubmitting || !formik.isValid}
              >
                {formik.isSubmitting && (
                  <i className="far fa-spinner-third fa-pulse button__icon"></i>
                )}

                {!formik.isSubmitting && 'SALVAR LISTA'}
              </button>
            </div>
          </form>
        </>
      )}

      {showSuccess && (
        <>
          <i className={'fal fa-check-circle modal__icon'}></i>

          <h2 className={'modal__title'}>Lista salva com sucesso!</h2>

          <p className={'modal__paragraph'}>
            Sua lista foi criada, agora você pode incluir, excluir e editar
            itens da sua mudança quando quiser.
          </p>

          <div className={'modal__group'}>
            <button
              className={'button button--secondary'}
              onClick={() => {
                handleClose();

                if (!userEmail) {
                  const returnUrl = window.location.search
                    ? window.location.search.replace('showMyLists=true', '') +
                      '&showMyLists=true'
                    : `${window.location.href}?showMyLists=true`;

                  window.vtexid.start({
                    returnUrl,
                    locale: 'pt-BR',
                    forceReload: false,
                  });

                  return;
                }

                setTimeout(() => {
                  setShowModalMoveList(true);
                }, 400);
              }}
            >
              VER MINHAS LISTAS
            </button>

            <button className={'button button--primary'} onClick={handleClose}>
              CONTINUAR
            </button>
          </div>
        </>
      )}
    </Modal>
  );
};

export default ModalSaveMoveList;
