// Dependencies
import React, { useState, useEffect } from 'react';
import classnames from 'classnames';
import { useSelector } from 'react-redux';

// Components
import Modal from '../Modal';
import MoveListItem from '../MoveListItem';

// Services
import CartService from 'Services/b8one.cartService';

// Repositories
import MoveListSaveRepository from 'Repositories/b8one.moveListSaveRepository';

const moveListSaveRepository = new MoveListSaveRepository();
const cartService = new CartService();

// ModalSaveMoveList component
const ModalMoveList = ({ showModal, handleClose }) => {
  const [userLists, setUserLists] = useState([]);
  const [isFetching, setIsFetching] = useState(false);
  const [isSaving, setIsSaving] = useState(false);
  const [deletingLists, setDeletingLists] = useState([]);
  const [userListSelected, setUserListSelected] = useState('');
  const [isButtonSeeListDisabled, setIsButtonSeeListDisabled] = useState(true);
  const [showNoListSelectedError, setShowNoListSelectedError] = useState(false);

  const userEmail = useSelector(({ user }) => user.email);

  useEffect(() => {
    async function fetchUserLists() {
      try {
        setIsFetching(true);

        const lists = await moveListSaveRepository.getAllListsByEmail(
          userEmail
        );

        setUserLists(lists);
        setIsFetching(false);
      } catch (error) {
        console.error(error);
      }
    }

    userEmail && fetchUserLists();
  }, []);

  useEffect(() => {
    async function fetchUserLists() {
      try {
        setIsFetching(true);

        const lists = await moveListSaveRepository.getAllListsByEmail(
          userEmail
        );

        setUserLists(lists);
        setIsFetching(false);
      } catch (error) {
        console.error(error);
      }
    }

    userEmail && fetchUserLists();
  }, [userEmail]);

  const handleButtonSeeListClick = async () => {
    !userListSelected && setShowNoListSelectedError(true);

    if (userListSelected) {
      setIsSaving(true);

      await cartService.setSingleCustomDataField(
        'orderinfo',
        'moveListSaveSelected',
        userListSelected
      );

      window.location.assign('/mudanca?isSavedList=true');
    }
  };

  const handleListItemClick = id => {
    setShowNoListSelectedError(false);

    isButtonSeeListDisabled && setIsButtonSeeListDisabled(false);

    id === userListSelected ? setUserListSelected('') : setUserListSelected(id);
  };

  const handleListItemButtonDeleteClick = async id => {
    if (deletingLists.includes(id)) return;

    try {
      const newDeletingLists = [...new Set([...deletingLists, id])];

      setDeletingLists(newDeletingLists);

      await moveListSaveRepository.deleteListByDocumentId(id);

      setTimeout(() => {
        const newUserLists = userLists.filter(userList => userList.id !== id);

        const newDeletingLists = deletingLists.filter(
          deletingList => deletingList !== id
        );

        setUserLists(newUserLists);
        setDeletingLists(newDeletingLists);

        userListSelected === id && setUserListSelected('');
      }, 1000);
    } catch (error) {
      console.error(error);
    }
  };

  return (
    <Modal
      handleClose={() => {
        handleClose();
      }}
      modalClass={classnames('modal-move-list', {
        'wrapper-modal--show': showModal,
      })}
    >
      {isFetching && (
        <i className={'far fa-spinner-third fa-pulse modal__loading'}></i>
      )}

      {!isFetching && (
        <>
          <h2 className={'modal__title'}>Selecione uma lista</h2>

          {userLists.length > 0 && (
            <p className={'modal__paragraph'}>
              Você pode escolher entre as listas salvas na sua conta
            </p>
          )}

          {userLists.length === 0 && (
            <p className={'modal__paragraph'}>Não tem listas cadastradas</p>
          )}

          <ul className={'modal__list'}>
            {userLists.map(({ id, listName, listDate }) => (
              <MoveListItem
                id={id}
                key={id}
                name={listName}
                date={listDate}
                error={showNoListSelectedError}
                active={id === userListSelected}
                deleting={deletingLists.includes(id)}
                handleListItemClick={() => handleListItemClick(id)}
                handleListItemButtonDeleteClick={() =>
                  handleListItemButtonDeleteClick(id)
                }
              />
            ))}

            {showNoListSelectedError && (
              <li className={'list__status list__status--error'}>
                <i className={'status__icon fas fa-exclamation-triangle'}></i>

                <p className={'status__text'}>
                  Você precisa selecionar uma das listas para continuar
                </p>
              </li>
            )}
          </ul>

          {userLists.length > 0 && (
            <button
              disabled={isButtonSeeListDisabled || isSaving}
              className={'modal__button js-button-see-list'}
              onClick={handleButtonSeeListClick}
            >
              {isSaving && (
                <i className={'far fa-spinner-third fa-pulse button__icon'}></i>
              )}

              {!isSaving && 'VER LISTA'}
            </button>
          )}
        </>
      )}
    </Modal>
  );
};

export default ModalMoveList;
