// Dependencies
import React, { useEffect, useState } from 'react';
import classnames from 'classnames';
import { useDispatch } from 'react-redux';

// Components
import Modal from '../../../components/Modal';
import MoveListSaveRepository from '../../../../general/repositories/b8one.moveListSaveRepository';
import HouseOptionsRepository from '../../../../general/repositories/b8one.houseOptionsRepository';

// Redux actions
import { Creators as productsActions } from '../../../../state/ducks/products';

// Utils
import isMobile from 'Utils/isMobile';

const moveListSaveRepository = new MoveListSaveRepository();
const houseOptionsRepository = new HouseOptionsRepository();

const ModalMovingInfo = ({ showModal, handleClose, handleReturn }) => {
  const [isLoading, setIsLoading] = useState(false);
  const [isRemovingProducts, setIsRemovingProducts] = useState(false);
  const [homeTypeValue, setHomeTypeValue] = useState('');
  const [dormQuantity, setDormQuantity] = useState('');
  const [homeSizeValue, setHomeSizeValue] = useState('');
  const [houseOptions, setHouseOptions] = useState({});
  const [fromAddress, setFromAddress] = useState('');
  const [toAddress, setToAddress] = useState('');
  const dispatch = useDispatch();

  useEffect(() => {
    getAddress(), handleHouseOptions();
  }, []);

  const handleHomeSizeChange = event => {
    const value = event.target.value;

    setHomeSizeValue(value);
  };

  const handleHomeTypeChange = event => {
    const value = event.target.value;

    setHomeTypeValue(value);
  };

  const handledormQuantityChange = event => {
    const value = event.target.value;

    setDormQuantity(value);
  };

  const handleReturnButton = () => {
    handleReturn();
  };

  const handleSubmitButton = async () => {
    setIsLoading(true);

    const checkbox = document.querySelector('#show-select-products');

    const query = `(listRooms=${dormQuantity} AND homeType=${homeTypeValue} AND homeSize=${homeSizeValue})`;

    // const query = `(homeType=${homeType} AND homeSize=${homeSize} AND listRooms=${dormQuantity})`;

    const result = await moveListSaveRepository.getListByQuery(query, [
      'listItems',
    ]);

    const selectElement = document.querySelectorAll('.modal__select');

    if (result.length < 1) {
      selectElement.forEach(item => (item.selectedIndex = 0));
      setHomeSizeValue('');
      setHomeTypeValue('');
      setDormQuantity('');
      setIsLoading(false);

      return;
    }

    const listItems = JSON.parse(result[0].listItems);

    await window.SelectItems.removeProducts();

    const items = listItems.map(({ skuId, quantity, seller = 1 }) => ({
      seller,
      quantity,
      id: skuId,
    }));

    console.log(items);

    const orderForm = await window.vtexjs.checkout.addToCart(items);

    window.SelectItems.updateOrderForm(orderForm);

    listItems.forEach(({ productId, quantity }) => {
      dispatch(productsActions.updateProductQuantity(productId, quantity));
    });

    checkbox.click();

    setIsLoading(false);
    handleClose();
  };

  const handleHouseOptions = async () => {
    const data = await houseOptionsRepository.getHouseOptions();

    const homeTypeFormatted = data[0].homeType.split(',');
    const homeSizeFormatted = data[0].homeSize.split(',');
    const listRoomsFormatted = data[0].listRooms.split(',');

    setHouseOptions(
      {
        homeType: homeTypeFormatted,
        homeSize: homeSizeFormatted,
        listRooms: listRoomsFormatted,
      },
      console.log('setstate finished')
    );
  };

  const getAddress = async () => {
    const { customData } = await vtexjs.checkout.getOrderForm();
    const { fields } = customData.customApps[0];

    setFromAddress(
      `${fields.from_address_county ? fields.from_address_county : ''}, ${
        fields.from_state
      }, ${fields.from_address_zipcode}`
    );

    setToAddress(
      `${fields.to_address_county ? fields.to_address_county : ''}, ${
        fields.to_state
      }, ${fields.to_address_zipcode}`
    );
  };

  return (
    <Modal
      showCloseButton={!isLoading && !isRemovingProducts}
      handleClose={() => handleClose()}
      modalClass={classnames('modal-moving-guide', {
        'wrapper-modal--show': showModal,
      })}
    >
      <>
        <h2 className={'modal__title'}>Conte-nos mais sobre sua mudança</h2>

        {/* <div className="modal__addressItem">
          <div className="modal__addressWrapper">
            <div className="modal__home modal__home--from">
              <p className="modal__homeText modal__homeText--from">Estou em</p>
            </div>
            <p className="modal__address">{fromAddress}</p>
          </div>
          <div className="modal__addressWrapper">
            <div className="modal__home modal__home--to">
              <p className="modal__homeText modal__homeText--to">Vou para</p>
            </div>
            <p className="modal__address">{toAddress}</p>
          </div>
        </div> */}

        <div className="modal__homeInfo">
          <div className="modal__homeItem">
            <p className="modal__selectTitle">Membros da família</p>
            <select
              name=""
              id=""
              className="modal__select"
              onChange={handleHomeTypeChange}
            >
              <option value="">Selecione</option>
              {houseOptions?.homeType?.map((item, index) => {
                return (
                  <option key={item} value={item}>
                    {item} {index === 0 ? 'pessoa' : 'pessoas'}
                  </option>
                );
              })}
            </select>
          </div>
          <div className="modal__homeItem">
            <p className="modal__selectTitle">Dormitórios</p>
            <select
              name=""
              id=""
              className="modal__select"
              onChange={handledormQuantityChange}
            >
              <option value="">Selecione</option>
              {houseOptions?.listRooms?.map((item, index) => {
                return (
                  <option key={item} value={item}>
                    {item} {index === 0 ? 'dormitório' : 'dormitórios'}
                  </option>
                );
              })}
            </select>
          </div>
          <div className="modal__homeItem">
            <p className="modal__selectTitle">Tamanho do Imóvel</p>
            <select
              name=""
              id=""
              className="modal__select"
              onChange={handleHomeSizeChange}
            >
              <option value="">Selecione</option>
              {houseOptions?.homeSize?.map((item, index) => {
                return (
                  <option key={item} value={item}>
                    {index === 0
                      ? 'até ' + item + 'm2'
                      : index > 0 && index < 4
                      ? 'de ' +
                        (parseInt(houseOptions.homeSize[index - 1]) + 1) +
                        'm2 até ' +
                        item +
                        'm2'
                      : 'mais de ' + item + 'm2'}
                  </option>
                );
              })}
            </select>
          </div>
        </div>

        <button
          className="modal__button--big"
          disabled={!homeTypeValue || !dormQuantity || !homeSizeValue}
          onClick={handleSubmitButton}
        >
          {!isLoading && 'AVANÇAR'}

          {isLoading && (
            <i className="far fa-spinner-third fa-pulse button__icon"></i>
          )}
        </button>
        <button className="modal__button--small" onClick={handleReturnButton}>
          VOLTAR
        </button>
      </>
    </Modal>
  );
};

export default ModalMovingInfo;
