// Dependencies
import React, { useEffect } from 'react';
import { useDispatch } from 'react-redux';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';

// Routes config
import routes from './routes';

// Services
import VtexUser from '../general/services/b8one.vtexUser';

// Redux
import { Creators as userActions } from '../state/ducks/user';

const vtexUserInstance = new VtexUser();

// App component
const App = () => {
  const dispatch = useDispatch();

  useEffect(() => {
    async function getUserEmail() {
      try {
        const userData = await vtexUserInstance.getCurrent();

        if (!userData) return;

        const { user } = userData;

        dispatch(userActions.setEmail(user));
      } catch (error) {
        console.error(error);
      }
    }

    getUserEmail();
  }, []);

  return (
    <Router>
      <Switch>
        {routes.map((route, index) => (
          <Route
            key={index}
            path={route.path}
            exact={route.exact}
            component={route.component}
          />
        ))}
      </Switch>
    </Router>
  );
};

export default App;
