import { Helper } from '../../common';

export default function isMobile() {
  if (
    process.env.NODE_ENV === 'development' &&
    window.location.pathname.includes('B8TM')
  ) {
    return true;
  }

  const VTEX_ISSMB_COOKIE = Helper.getCookie('ISSMB');

  if (
    VTEX_ISSMB_COOKIE &&
    VTEX_ISSMB_COOKIE.includes('UserAcceptMobile=True')
  ) {
    return true;
  }

  return false;
}
